import Icon from "@/components/Icon.vue";
import styles from "./showMessage.module.less";
import getcomponentDom from "./getcomponentDom.js";
/**
 *
 * @param {String} content 
 * @param {String} type 
 * @param {Number} duration 
 * @param {HTMLelement} container 
 */
export default function(option = {}) {
  const content = option.content || "";
  const type = option.type || "info";
  const duration = option.duration || 2000;
  const container = option.container || document.body;
  const div = document.createElement("div");
  const Dom = getcomponentDom(Icon, {
    type,
  });
  const typeClassName = styles[`message-${type}`];
  div.innerHTML = `<span class=${styles.icon}>${Dom.outerHTML}</span><div>${content}</div>`;
  div.className = `${styles.message} ${typeClassName}`;

  if (!container) {
    if (getComputedStyle(container).position === "static") {
      container.style.position = "relative";
    }
  }
  //div
  container.appendChild(div);
  // 
  div.clientHeight; //

  //
  div.style.opacity = 1;
  div.style.transform = `translate(-50%,-50%)`;
  setTimeout(() => {
    div.style.opacity = 0;
    div.style.transform = `translate(-50%,-50%) translateY(-25px)`;
    div.addEventListener(
      "transitionend",
      () => {
        div.remove();
        option.callBye && option.callBye();
      },
      { once: true }
    );
  }, duration);
}
