export default function(dom) {
  return {
    methods: {
      scroll() {
        this.$bus.$emit("mainScroll", this.$refs[dom]);
      },
      handleClick(num) {
        this.$refs[dom].scrollTop = num;
      },
    },

    mounted() {
      this.$refs[dom].addEventListener("scroll", this.scroll);
      this.$bus.$on("setClick", this.handleClick);
    },
    //
    beforeDestroy() {
      this.$bus.$emit("mainScroll"); //undefinedtop
      this.$bus.$off("setClick", this.handleClick);
      this.$refs[dom].removeEventListener("scroll", this.scroll);
    },
  };
}
