import ins from "./request";
//
export async function getBlogCategories() {
  return await ins.get("/api/blogtype");
}
//
export async function getBlog(page = 1, limit = 10, categoryid = -1) {
  return await ins.get("/api/blog", {
    params: {
      page,
      limit,
      categoryid,
    },
  });
}
//
export async function getBlogs(id) {
  return await ins.get(`/api/blog/${id}`);
}
//
export async function submitComments(commentId) {
  return await ins.post("/api/comment", commentId);
}
//
export async function getComment(blogId, page = 1, limit = 10) {
  return await ins.get(`/api/comment`, {
    params: { blogId, page, limit },
  });
}
//
export async function getGlobleSett() {
  return await ins.get("/api/setting");
}
